<template>
	<div>
		<el-dialog v-model="formVisible" :title="formTitle" width="80%" destroy-on-close :fullscreen='false'>
			<el-form class="formModel_form" ref="formRef" :model="form" label-width="$template2.back.add.form.base.labelWidth" :rules="rules">
				<el-row>
					<el-col :span="12">
						<el-form-item label="" prop="shiyanmingcheng">
							<el-input class="list_inp" v-model="form.shiyanmingcheng" placeholder=""
								 type="text" 								:readonly="!isAdd||disabledForm.shiyanmingcheng?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="" prop="shiyanriqi">
							<el-date-picker
								class="list_date"
								v-model="form.shiyanriqi"
								format="YYYY-MM-DD HH:mm:ss"
								value-format="YYYY-MM-DD HH:mm:ss"
								type="datetime"
								:readonly="!isAdd||disabledForm.shiyanriqi?true:false"
								placeholder="" />
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item label="" prop="keyanzhanghao">
							<el-input class="list_inp" v-model="form.keyanzhanghao" placeholder=""
								 type="text" 								:readonly="!isAdd||disabledForm.keyanzhanghao?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="" prop="keyanrenyuanxingming">
							<el-input class="list_inp" v-model="form.keyanrenyuanxingming" placeholder=""
								 type="text" 								:readonly="!isAdd||disabledForm.keyanrenyuanxingming?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="" prop="shujuleixing">
							<el-input class="list_inp" v-model="form.shujuleixing" placeholder=""
								 type="text" 								:readonly="!isAdd||disabledForm.shujuleixing?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="" prop="shujudanwei">
							<el-input class="list_inp" v-model="form.shujudanwei" placeholder=""
								 type="text" 								:readonly="!isAdd||disabledForm.shujudanwei?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="" prop="yangpinmingcheng">
							<el-input class="list_inp" v-model="form.yangpinmingcheng" placeholder=""
								 type="text" 								:readonly="!isAdd||disabledForm.yangpinmingcheng?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="" prop="yuanshishuju">
							<el-input class="list_inp" v-model="form.yuanshishuju" placeholder=""
								 type="text" 								:readonly="!isAdd||disabledForm.yuanshishuju?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="" prop="chulishuju">
							<el-input class="list_inp" v-model="form.chulishuju" placeholder=""
								 type="text" 								:readonly="!isAdd||disabledForm.chulishuju?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="" prop="wuchafanwei">
							<el-input class="list_inp" v-model="form.wuchafanwei" placeholder=""
								 type="text" 								:readonly="!isAdd||disabledForm.wuchafanwei?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="" prop="shujuzhushi">
							<el-input class="list_inp" v-model="form.shujuzhushi" placeholder=""
								 type="text" 								:readonly="!isAdd||disabledForm.shujuzhushi?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="" prop="shiyantiaojian">
							<el-input class="list_inp" v-model="form.shiyantiaojian" placeholder=""
								 type="text" 								:readonly="!isAdd||disabledForm.shiyantiaojian?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="" prop="jielun">
							<el-input class="list_inp" v-model="form.jielun" placeholder=""
								 type="text" 								:readonly="!isAdd||disabledForm.jielun?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="" prop="shiyanzhuangtai">
							<el-select
								class="list_sel"
								:disabled="!isAdd||disabledForm.shiyanzhuangtai?true:false"
								v-model="form.shiyanzhuangtai" 
								placeholder=""
								>
								<el-option v-for="(item,index) in shiyanzhuangtaiLists" :label="item"
									:value="item"
									>
								</el-option>
							</el-select>
						</el-form-item>
					</el-col>
				</el-row>
			</el-form>
			<template #footer v-if="isAdd||type=='logistics'||type=='reply'">
				<span class="formModel_btn_box">
					<el-button class="formModel_cancel" @click="closeClick"></el-button>
					<el-button class="formModel_confirm" type="primary" @click="save"
						>
						
					</el-button>
				</span>
			</template>
		</el-dialog>
	</div>
</template>
<script setup>
	import {
		reactive,
		ref,
		getCurrentInstance,
		nextTick,
		computed,
		defineEmits
	} from 'vue'
	const context = getCurrentInstance()?.appContext.config.globalProperties;	
	const emit = defineEmits(['formModelChange'])
	//
	const tableName = 'shiyanshuju'
	const formName = ''
	//
	//form
	const form = ref({})
	const disabledForm = ref({
		shiyanmingcheng : false,
		shiyanriqi : false,
		keyanzhanghao : false,
		keyanrenyuanxingming : false,
		shujuleixing : false,
		shujudanwei : false,
		yangpinmingcheng : false,
		yuanshishuju : false,
		chulishuju : false,
		wuchafanwei : false,
		shujuzhushi : false,
		shiyantiaojian : false,
		jielun : false,
		shiyanzhuangtai : false,
	})
	const formVisible = ref(false)
	const isAdd = ref(false)
	const formTitle = ref('')
	//
	//
	const validateIntNumber = (rule, value, callback) => {
		if (!value) {
			callback();
		} else if (!context?.$toolUtil.isIntNumer(value)) {
			callback(new Error(""));
		} else {
			callback();
		}
	}
	//
	const validateNumber = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isNumber(value)) {
			callback(new Error(""));
		} else {
			callback();
		}
	}
	//
	const validateMobile = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isMobile(value)) {
			callback(new Error(""));
		} else {
			callback();
		}
	}
	//
	const validatePhone = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isPhone(value)) {
			callback(new Error(""));
		} else {
			callback();
		}
	}
	//
	const validateEmail = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isEmail(value)) {
			callback(new Error(""));
		} else {
			callback();
		}
	}
	//
	const validateIdCard = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.checkIdCard(value)) {
			callback(new Error(""));
		} else {
			callback();
		}
	}
	//
	const validateUrl = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isURL(value)) {
			callback(new Error("URL"));
		} else {
			callback();
		}
	}
	const rules = ref({
		shiyanmingcheng: [
		],
		shiyanriqi: [
		],
		keyanzhanghao: [
		],
		keyanrenyuanxingming: [
		],
		shujuleixing: [
		],
		shujudanwei: [
		],
		yangpinmingcheng: [
		],
		yuanshishuju: [
		],
		chulishuju: [
		],
		wuchafanwei: [
		],
		shujuzhushi: [
		],
		shiyantiaojian: [
		],
		jielun: [
		],
		shiyanzhuangtai: [
		],
	})
	//
	
	const formRef = ref(null)
	const id = ref(0)
	const type = ref('')
	//
	const shiyanzhuangtaiLists = ref([])
	//methods

	//
	const getUUID =()=> {
      return new Date().getTime();
    }
	//
	const resetForm = () => {
		form.value = {
			shiyanmingcheng: '',
			shiyanriqi: '',
			keyanzhanghao: '',
			keyanrenyuanxingming: '',
			shujuleixing: '',
			shujudanwei: '',
			yangpinmingcheng: '',
			yuanshishuju: '',
			chulishuju: '',
			wuchafanwei: '',
			shujuzhushi: '',
			shiyantiaojian: '',
			jielun: '',
			shiyanzhuangtai: '',
		}
	}
	//info
	const getInfo = ()=>{
		context?.$http({
			url: `${tableName}/info/${id.value}`,
			method: 'get'
		}).then(res => {
			let reg=new RegExp('../../../file','g')
			form.value = res.data.data
			formVisible.value = true
		})
	}
	const crossRow = ref('')
	const crossTable = ref('')
	const crossTips = ref('')
	const crossColumnName = ref('')
	const crossColumnValue = ref('')
	//
	const init=(formId=null,formType='add',formNames='',row=null,table=null,statusColumnName=null,tips=null,statusColumnValue=null)=>{
		resetForm()
		if(formId){
			id.value = formId
			type.value = formType
		}
		if(formType == 'add'){
			isAdd.value = true
			formTitle.value = '' + formName
			formVisible.value = true
		}else if(formType == 'info'){
			isAdd.value = false
			formTitle.value = '' + formName
			getInfo()
		}else if(formType == 'edit'){
			isAdd.value = true
			formTitle.value = '' + formName
			getInfo()
		}
		else if(formType == 'cross'){
			isAdd.value = true
			formTitle.value = formNames
			// getInfo()
			for(let x in row){
				if(x=='shiyanmingcheng'){
					form.value.shiyanmingcheng = row[x];
					disabledForm.value.shiyanmingcheng = true;
					continue;
				}
				if(x=='shiyanriqi'){
					form.value.shiyanriqi = row[x];
					disabledForm.value.shiyanriqi = true;
					continue;
				}
				if(x=='keyanzhanghao'){
					form.value.keyanzhanghao = row[x];
					disabledForm.value.keyanzhanghao = true;
					continue;
				}
				if(x=='keyanrenyuanxingming'){
					form.value.keyanrenyuanxingming = row[x];
					disabledForm.value.keyanrenyuanxingming = true;
					continue;
				}
				if(x=='shujuleixing'){
					form.value.shujuleixing = row[x];
					disabledForm.value.shujuleixing = true;
					continue;
				}
				if(x=='shujudanwei'){
					form.value.shujudanwei = row[x];
					disabledForm.value.shujudanwei = true;
					continue;
				}
				if(x=='yangpinmingcheng'){
					form.value.yangpinmingcheng = row[x];
					disabledForm.value.yangpinmingcheng = true;
					continue;
				}
				if(x=='yuanshishuju'){
					form.value.yuanshishuju = row[x];
					disabledForm.value.yuanshishuju = true;
					continue;
				}
				if(x=='chulishuju'){
					form.value.chulishuju = row[x];
					disabledForm.value.chulishuju = true;
					continue;
				}
				if(x=='wuchafanwei'){
					form.value.wuchafanwei = row[x];
					disabledForm.value.wuchafanwei = true;
					continue;
				}
				if(x=='shujuzhushi'){
					form.value.shujuzhushi = row[x];
					disabledForm.value.shujuzhushi = true;
					continue;
				}
				if(x=='shiyantiaojian'){
					form.value.shiyantiaojian = row[x];
					disabledForm.value.shiyantiaojian = true;
					continue;
				}
				if(x=='jielun'){
					form.value.jielun = row[x];
					disabledForm.value.jielun = true;
					continue;
				}
				if(x=='shiyanzhuangtai'){
					form.value.shiyanzhuangtai = row[x];
					disabledForm.value.shiyanzhuangtai = true;
					continue;
				}
			}
			if(row){
				crossRow.value = row
			}
			if(table){
				crossTable.value = table
			}
			if(tips){
				crossTips.value = tips
			}
			if(statusColumnName){
				crossColumnName.value = statusColumnName
			}
			if(statusColumnValue){
				crossColumnValue.value = statusColumnValue
			}
			formVisible.value = true
		}

		context?.$http({
			url: `${context?.$toolUtil.storageGet('sessionTable')}/session`,
			method: 'get'
		}).then(res => {
			var json = res.data.data
			if(json.hasOwnProperty('keyanzhanghao')&& context?.$toolUtil.storageGet("role")!=""){
				form.value.keyanzhanghao = json.keyanzhanghao
				disabledForm.value.keyanzhanghao = true;
			}
			if(json.hasOwnProperty('keyanrenyuanxingming')&& context?.$toolUtil.storageGet("role")!=""){
				form.value.keyanrenyuanxingming = json.keyanrenyuanxingming
				disabledForm.value.keyanrenyuanxingming = true;
			}
		})
		shiyanzhuangtaiLists.value = ",".split(',')
	}
	//
	//
	defineExpose({
		init
	})
	//
	const closeClick = () => {
		formVisible.value = false
	}
	//
	const editorChange = (e,name) =>{
		form.value[name] = e
	}
	//
	const save=()=>{
		var table = crossTable.value
		var objcross = JSON.parse(JSON.stringify(crossRow.value))
		let crossUserId = ''
		let crossRefId = ''
		let crossOptNum = ''
		if(type.value == 'cross'){
			if(crossColumnName.value!=''){
				if(!crossColumnName.value.startsWith('[')){
					for(let o in objcross){
						if(o == crossColumnName.value){
							objcross[o] = crossColumnValue.value
						}
					}
					//
					changeCrossData(objcross)
				}else{
					crossUserId = context?.$toolUtil.storageGet('userid')
					crossRefId = objcross['id']
					crossOptNum = crossColumnName.value.replace(/\[/,"").replace(/\]/,"")
				}
			}
		}
		formRef.value.validate((valid)=>{
			if(valid){
				if(crossUserId&&crossRefId){
					form.value.crossuserid = crossUserId
					form.value.crossrefid = crossRefId
					let params = {
						page: 1,
						limit: 1000, 
						crossuserid:form.value.crossuserid,
						crossrefid:form.value.crossrefid,
					}
					context?.$http({
						url: `${tableName}/page`,
						method: 'get', 
						params: params 
					}).then(res=>{
						if(res.data.data.total>=crossOptNum){
							context?.$toolUtil.message(`${crossTips.value}`,'error')
							return false
						}else{
							context?.$http({
								url: `${tableName}/${!form.value.id ? "save" : "update"}`,
								method: 'post', 
								data: form.value 
							}).then(res=>{
								context?.$toolUtil.message(``,'success',()=>{
									formVisible.value = false
									emit('formModelChange')
								})
							})
						}
					})
				}else{
					context?.$http({
						url: `${tableName}/${!form.value.id ? "save" : "update"}`,
						method: 'post', 
						data: form.value 
					}).then(res=>{
						context?.$toolUtil.message(``,'success',()=>{
							formVisible.value = false
							emit('formModelChange')
						})
					})
				}
			}
		})
	}
	//
	const changeCrossData=(row)=>{
		context?.$http({
			url: `${crossTable.value}/update`,
			method: 'post',
			data: row
		}).then(res=>{})
	}
</script>
<style lang="scss" scoped>
	// 
	.formModel_form{
		border: 0px solid #ddd;
		border-radius: 4px;
		padding: 30px;
		margin: 0;
		background: #fff;
		// form item
		:deep(.el-form-item) {
			margin: 0 150px 20px 0;
			background: none;
			display: flex;
			//label
			.el-form-item__label {
			 background: none;
			 font-weight: 500;
			 display: block;
			 width: 150px;
			 min-width: 150px;
			 text-align: right;
			}
			// 
			.el-form-item__content {
				display: flex;
				width: calc(100% - 120px);
				justify-content: flex-start;
				align-items: center;
				flex-wrap: wrap;
				// 
				.list_inp {
					border: 1px solid #ddd;
					border-radius: 0px;
					padding: 0 10px;
					width: auto;
					line-height: 36px;
					box-sizing: border-box;
					min-width: 350px;
					height: 36px;
					//
					.el-input__wrapper{
						border: none;
						box-shadow: none;
						background: none;
						border-radius: 0;
						height: 100%;
						padding: 0;
					}
					.is-focus {
						box-shadow: none !important;
					}
				}
				//
				.list_date {
					border: 1px solid #ddd;
					border-radius: 0px;
					width: auto;
					line-height: 36px;
					box-sizing: border-box;
					min-width: 350px;
					//
					.el-input__wrapper{
						border: none;
						box-shadow: none;
						background: none;
						border-radius: 0;
						height: 100%;
					}
				}
				// 
				.list_sel {
					border: 1px solid #ddd;
					border-radius: 0px;
					padding: 0 10px;
					width: auto;
					line-height: 36px;
					box-sizing: border-box;
					min-width: 350px;
					//
					.select-trigger{
						height: 100%;
						.el-input{
							height: 100%;
							.el-input__wrapper{
								border: none;
								box-shadow: none;
								background: none;
								border-radius: 0;
								height: 100%;
								padding: 0;
							}
							.is-focus {
								box-shadow: none !important;
							}
						}
					}
				}
			}
		}
	}
	// 
	.formModel_btn_box {
		display: flex;
		width: 100%;
		justify-content: center;
		align-items: center;
		.formModel_cancel {
			border: 0;
			cursor: pointer;
			border-radius: 0px;
			padding: 0 24px;
			margin: 0 20px 0 0;
			outline: none;
			color: #fff;
			background: #999;
			width: auto;
			font-size: 14px;
			min-width: 100px;
			height: 36px;
		}
		.formModel_cancel:hover {
		}
		
		.formModel_confirm {
			border: 1px solid #f69a28;
			cursor: pointer;
			border-radius: 0px;
			padding: 0 24px;
			margin: 0 20px 0 0;
			outline: none;
			color: #fff;
			background: linear-gradient(270deg, rgba(246,154,40,1) 0%, rgba(255,186,101,1) 50%, rgba(246,154,40,1) 100%);
			width: auto;
			font-size: 14px;
			min-width: 100px;
			height: 36px;
		}
		.formModel_confirm:hover {
		}
	}
</style>